import Vue from 'vue'
import VueRouter from 'vue-router'

const Main = () => import(/* webpackChunkName: "Main" */ '@/views/Main')
const Home = () => import(/* webpackChunkName: "Home" */ '@/views/Home')
const Article = () => import(/* webpackChunkName: "Article" */ '@/views/Article')
const Hero = () => import(/* webpackChunkName: "Hero" */ '@/views/Hero')

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: Main,
    children: [
      { path: '/', name: 'home', component: Home },
      { path: '/articles/:id', name: 'article', component: Article, props: true }
    ]
  },
  { path: '/heroes/:id', name: 'hero', component: Hero, props: true }
]

const router = new VueRouter({
  routes
})

export default router
